import { getVirtualHostOptions, defaultProps } from '../common/component-handler/press-component';
import { style } from '../common/utils/style';


import { clickOutsideMixin } from '../mixins/basic/click-outside';



export default {
  name: 'PressPopoverPlus',
  options: {
    ...getVirtualHostOptions(true, true),
  },
  mixins: [

    clickOutsideMixin({
      event: 'touchstart',
      method: 'onClickOutside',
    }),

  ],
  props: {
    show: {
      type: Boolean,
      default: true,
    },
    placement: {
      type: String,
      default: 'right',
    },
    popperClass: {
      type: String,
      default: '',
    },
    ...defaultProps,
    customStyle: {
      type: [String, Object],
      default: '',
    },
  },

  emits: [

    'close',

  ],
  data() {
    return {
      innerShow: false,
      isEnter: false,

      watchShowTimer: null,
    };
  },
  computed: {
    wrapStyle() {
      return style(this.customStyle);
    },
  },
  watch: {
    show: {
      handler(value) {
        if (value) {
          clearTimeout(this.watchShowTimer);

          this.innerShow = value;
          this.isEnter = value;
        } else {
          // remove
          this.isEnter = value;
          clearTimeout(this.watchShowTimer);

          this.watchShowTimer = setTimeout(() => {
            this.innerShow = value;
          }, 300);
        }
      },
      immediate: true,
    },
  },
  methods: {
    onClickOutside() {
      this.$emit('close');
    },
  },
};

